<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 18,  //当前表格的名称编号
        setting: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        // this.setting = {'1_1': 10, '2_1': 5};
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        //第1行全部计算
        this.fn1(1, 9, [2, 3, 4, 5, 6, 7], [6, 7, 9])
        //第10行全部计算
        this.fn1(10, 9, [11, 12, 13], [7, 9])
        //第18行全部计算
        this.fn1(18, 9, [19, 20], [6, 7, 9])
        //第21行全部运算
        this.fn1(21, 9, [22, 23, 24, 25, 26, 27, 28, 30], [6, 7, 9])
        //第31行全部运算
        this.fn1(31, 9, [32, 33, 34, 35, 36], [6, 7, 9])
        //第39行全部运算
        this.fn1(39, 9, [1, 18, 21, 31, 37, 38], [])
        //第7列全部运算
        this.fn2(7, 39, [5, 6])
        //第9列全部运算
        this.fn2(9, 40, [2, 5])
      },
      // q 要计算单元格的行
      // l 要运算多少列
      // jump 参与运算的行
      // out  跳过运算的列
      // {
      // 1:a行a列=b行a列+c行a列...
      // }
      fn1(q, l, jump, out) {
        for (let i = 1; i <= l; i++) {
          if (J.inArray(i, out) < 0) {
            if (J('input[name=' + jump[0] + '_' + i + ']').val()) {
              var sum = parseFloat(J('input[name=' + jump[0] + '_' + i + ']').val())
            } else {
              var sum = 0.00
            }
            for (let k = 1; k < jump.length; k++) {
              let dom = J('input[name=' + jump[k] + '_' + i + ']')
              if (dom.val()) {
                sum += parseFloat(dom.val())
              }
            }
            J('input[name=' + q + '_' + i + ']').val(sum.toFixed(2))
          }
        }
      },
      // q 要运算的列
      // l 要运算多少行
      // jump 参与运算的列
      fn2(q, l, jump) {
        for (let i = 1; i <= l; i++) {
          if (J('input[name=' + i + '_' + jump[0] + ']').val()) {
            var sum = parseFloat(J('input[name=' + i + '_' + jump[0] + ']').val())
          } else {
            var sum = 0
          }
          for (let k = 1; k < jump.length; k++) {
            let dom = J('input[name=' + i + '_' + jump[k] + ']')
            if (dom.val()) {
              sum -= parseFloat(dom.val())
            }
          }
          J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
        }
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input, select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    padding: 3px 2px;
    text-align: left;
  }

  .bg-write {
    background: #d6ebff;
  }

  .bg-unwrite {
    background: #ccccff;
  }

  .bg-gray {
    background: #ddd;
  }

  .tdcon {
    text-align: center;
  }
</style>



